<template>
    <div class="modal fade" id="modalAddScriptParam" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="modalLabel">Add script param</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                <div>Param name: <input v-model="paramName"></div>

                <div>
                    Param type:
                    <input type="radio" id="type_string" value="string" v-model="paramType"><label for="type_string">String </label>
                    <input type="radio" id="type_number" value="number" v-model="paramType"><label for="type_number">Number </label>
                </div>

                <div>Default value: <input v-model="defaultValue"></div>
            </div>
            <div class="modal-footer">
                <button v-if="editingParam == null" type="button" class="btn btn-primary" v-on:click="addScriptParam()">Add</button>
                <button v-if="editingParam != null" type="button" class="btn btn-primary" v-on:click="updateScriptParam()">Update</button>
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
            </div>
            </div>
        </div>
    </div>
</template>

<script>
//import IconUp from '@/components/icons/IconUp.vue'
//import IconDown from '@/components/icons/IconDown.vue'

export default {
  name: 'ModalAddScriptParam',
  components: {
    //IconUp, IconDown
  },
  props: {
  },
  data() {
    return {
        editingParam: null,

        paramName: "",
        paramType: "string",
        defaultValue: ""
    }
  },
  methods: {
    open() {
        this.clearForm();
        window.$('#modalAddScriptParam').modal('show');
    },
    editScriptParam(param) {
        this.editingParam = param;

        this.paramName = this.editingParam.name;
        this.paramType = this.editingParam.type;
        this.defaultValue = this.editingParam.defaultValue;

        window.$('#modalAddScriptParam').modal('show');
    },
    updateScriptParam() {
        this.editingParam.name = this.paramName;
        this.editingParam.type = this.paramType;
        this.editingParam.defaultValue = this.paramType == "number" ? Number(this.defaultValue) : this.defaultValue;

        window.$('#modalAddScriptParam').modal('hide');
        this.clearForm();
    },
    addScriptParam() {
        let param = {
            name: this.paramName,
            type: this.paramType,
            defaultValue: this.paramType == "number" ? Number(this.defaultValue) : this.defaultValue
        };

        this.$parent.addScriptParam(param);
        window.$('#modalAddScriptParam').modal('hide');
        this.clearForm();
    },
    clearForm() {
        this.editingParam = null;

        this.paramName = "";
        this.paramType = "string";
        this.defaultValue = "";
    }
  }
}
</script>

