<template>
  <div>
    <div class="panelHeader" v-on:click="$parent.togglePanelVisibility('context')">
        <span class="panelTitle">Context</span>
    </div>
    <div v-show="$parent.visiblePanels.context" class="panelBody">
        <div class="buttonGroup">
            <button v-on:click="$parent.resetContext()" class="btn btn-primary btn-sm">Reset context</button>
            <button v-on:click="$parent.saveContext()" class="btn btn-primary btn-sm">Save context</button>
            <button v-on:click="$parent.restoreContext()" class="btn btn-primary btn-sm">Restore context</button>
            <!--<button v-on:click="$parent.clearFilters()" class="btn btn-primary btn-sm">Clear filters</button>-->
            <!--<button v-on:click="resetTransform()" class="btn btn-primary btn-sm">Reset Transform</button>-->
        </div>
        <table class="smallTable contextPropertiesTable">
            <tbody>
                <tr>
                    <td>fillStyle</td>
                    <td>
                        <span class="colorSquare" :style="'backgroundColor:' + $parent.ctx.fillStyle"></span>
                        <span style="line-height: 135%;" class="spanFillStyleStrokeStyle">{{ $parent.ctx.fillStyle }}</span>
                    </td>
                    <td>
                        <span v-on:click="editContextAttribute('fillStyle')" data-toggle="modal" data-target="#modalEditContextProperty">
                          <IconEdit></IconEdit>
                        </span>
                        <span v-on:click="$parent.resetContextAttribute('fillStyle')" v-show="$parent.ctx.fillStyle != '#000000'" class="spanIcon">
                            <IconReset></IconReset>
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>strokeStyle</td>
                    <td>
                        <span class="colorSquare" :style="'backgroundColor:' + $parent.ctx.strokeStyle"></span>
                        <span style="line-height: 135%;" class="spanFillStyleStrokeStyle">{{ $parent.ctx.strokeStyle }}</span>
                    </td>
                    <td>
                        <span v-on:click="editContextAttribute('strokeStyle')" data-toggle="modal" data-target="#modalEditContextProperty">
                          <IconEdit></IconEdit>
                        </span>
                        <span v-on:click="$parent.resetContextAttribute('strokeStyle')" v-show="$parent.ctx.strokeStyle != '#000000'" class="spanIcon">
                            <IconReset></IconReset>
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>font</td>
                    <td>{{ $parent.ctx.font }}</td>
                    <td>
                        <span v-on:click="editContextAttribute('font')" data-toggle="modal" data-target="#modalEditContextProperty">
                          <IconEdit></IconEdit>
                        </span>
                        <span v-on:click="$parent.resetContextAttribute('font')" v-show="$parent.ctx.font != '10px sans-serif'" class="spanIcon">
                            <IconReset></IconReset>
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>textAlign</td>
                    <td>{{ $parent.ctx.textAlign }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('textAlign')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('textAlign')" v-show="$parent.ctx.textAlign != 'start'" class="spanIcon">
                            <IconReset></IconReset>
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>globalAlpha</td>
                    <td>{{ $parent.ctx.globalAlpha }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('globalAlpha')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('globalAlpha')" v-show="$parent.ctx.globalAlpha != '1'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>globalCompositeOperation</td>
                    <td>{{ $parent.ctx.globalCompositeOperation }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('globalCompositeOperation')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('globalCompositeOperation')" v-show="$parent.ctx.globalCompositeOperation != 'source-over'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>lineWidth</td>
                    <td>{{ $parent.ctx.lineWidth }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('lineWidth')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('lineWidth')" v-show="$parent.ctx.lineWidth != '1'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>lineCap</td>
                    <td>{{ $parent.ctx.lineCap }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('lineCap')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('lineCap')" v-show="$parent.ctx.lineCap != 'butt'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>lineJoin</td>
                    <td>{{ $parent.ctx.lineJoin }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('lineJoin')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('lineJoin')" v-show="$parent.ctx.lineJoin != 'miter'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>miterLimit</td>
                    <td>{{ $parent.ctx.miterLimit }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('miterLimit')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('miterLimit')" v-show="$parent.ctx.miterLimit != 10" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>transform</td>
                    <td>{{ $parent.ctx.transform }}</td>
                    <td>
                        <IconEdit></IconEdit>
                        <span v-on:click="$parent.resetTransform()" class="spanIcon" v-show="$parent.ctx.transform != 'matrix(1, 0, 0, 1, 0, 0)'">
                            <IconReset></IconReset>
                        </span>
                    </td>
                </tr>
                <tr>
                    <td>filter</td>
                    <td>{{ $parent.ctx.filter }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('filter')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('filter')" v-show="$parent.ctx.filter != 'none'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>shadowBlur</td>
                    <td>{{ $parent.ctx.shadowBlur }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('shadowBlur')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('shadowBlur')" v-show="$parent.ctx.shadowBlur != '0'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>shadowColor</td>
                    <td>{{ $parent.ctx.shadowColor }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('shadowColor')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('shadowColor')" v-show="$parent.ctx.shadowColor != 'rgba(0, 0, 0, 0)'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>shadowOffsetX</td>
                    <td>{{ $parent.ctx.shadowOffsetX }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('shadowOffsetX')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('shadowOffsetX')" v-show="$parent.ctx.shadowOffsetX != '0'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>shadowOffsetY</td>
                    <td>{{ $parent.ctx.shadowOffsetY }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('shadowOffsetY')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('shadowOffsetY')" v-show="$parent.ctx.shadowOffsetY != '0'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr v-if="$parent.ctx.lineDash">
                    <td>lineDash</td>
                    <td>{{ $parent.ctx.lineDash }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('lineDash')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('lineDash')" v-show="$parent.ctx.lineDash.length != 0" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>lineDashOffset</td>
                    <td>{{ $parent.ctx.lineDashOffset }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('lineDashOffset')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('lineDashOffset')" v-show="$parent.ctx.lineDashOffset != 0" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
                <tr>
                    <td>textBaseline</td>
                    <td>{{ $parent.ctx.textBaseline }}</td>
                    <td>
                      <span v-on:click="editContextAttribute('textBaseline')" data-toggle="modal" data-target="#modalEditContextProperty">
                        <IconEdit></IconEdit>
                      </span>
                      <span v-on:click="$parent.resetContextAttribute('textBaseline')" v-show="$parent.ctx.textBaseline != 'alphabetic'" class="spanIcon">
                        <IconReset></IconReset>
                      </span>
                    </td>
                </tr>
            </tbody>
        </table>
        <div class="spacing"></div>
    </div>




    <!-- Modal -->
    <div class="modal fade" id="modalEditContextProperty" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="modalLabel">Edit context attribute</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div v-show="currentAttribute=='fillStyle'">fillStyle: <input v-model="newCtx.fillStyle"></div>
            <div v-show="currentAttribute=='strokeStyle'">strokeStyle: <input v-model="newCtx.strokeStyle"></div>
            <div v-show="currentAttribute=='font'">font: <input v-model="newCtx.font"></div>
            <div v-show="currentAttribute=='globalAlpha'">globalAlpha: <input v-model="newCtx.globalAlpha"></div>
            <div v-show="currentAttribute=='lineWidth'">lineWidth: <input v-model="newCtx.lineWidth"></div>

            <div v-show="currentAttribute=='lineCap'">
              lineCap: 
              <select v-model="newCtx.lineCap">
                <option value="butt">butt</option>
                <option value="round">round</option>
                <option value="square">square</option>
              </select>
            </div>

            <div v-show="currentAttribute=='lineJoin'">
              lineJoin:
              <select v-model="newCtx.lineJoin">
                <option value="miter">miter</option>
                <option value="bevel">bevel</option>
                <option value="round">round</option>
              </select>
            </div>

            <div v-show="currentAttribute=='filter'">filter: <input v-model="newCtx.filter"></div>
            <div v-show="currentAttribute=='shadowBlur'">shadowBlur: <input v-model="newCtx.shadowBlur"></div>
            <div v-show="currentAttribute=='shadowColor'">shadowColor: <input v-model="newCtx.shadowColor"></div>
            <div v-show="currentAttribute=='shadowOffsetX'">shadowOffsetX: <input v-model="newCtx.shadowOffsetX"></div>
            <div v-show="currentAttribute=='shadowOffsetY'">shadowOffsetY: <input v-model="newCtx.shadowOffsetY"></div>
            <div v-show="currentAttribute=='lineDash'">lineDash: <input v-model="newCtx.lineDashStr"></div>
            <div v-show="currentAttribute=='lineDashOffset'">lineDashOffset: <input v-model="newCtx.lineDashOffset"></div>

            <div v-show="currentAttribute=='textAlign'">
              textAlign:
              <select v-model="newCtx.textAlign">
                <option value="start">start</option>
                <option value="end">end</option>
                <option value="left">left</option>
                <option value="right">right</option>
                <option value="center">center</option>
              </select>
            </div>

            <div v-show="currentAttribute=='miterLimit'">miterLimit: <input v-model="newCtx.miterLimit"></div>

            <div v-show="currentAttribute=='textBaseline'">
              textBaseline:
              <select v-model="newCtx.textBaseline">
                <option value="alphabetic">alphabetic</option>
                <option value="top">top</option>
                <option value="hanging">hanging</option>
                <option value="middle">middle</option>
                <option value="ideographic">ideographic</option>
                <option value="bottom">bottom</option>
              </select>
            </div>

            <div v-show="currentAttribute=='globalCompositeOperation'">
              globalCompositeOperation:
              <select v-model="newCtx.globalCompositeOperation">
                <option value="source-over">source-over</option>
                <option value="source-in">source-in</option>
                <option value="source-out">source-out</option>
                <option value="source-atop">source-atop</option>
                <option value="destination-over">destination-over</option>
                <option value="destination-in">destination-in</option>
                <option value="destination-out">destination-out</option>
                <option value="destination-atop">destination-atop</option>
                <option value="lighter">lighter</option>
                <option value="copy">copy</option>
                <option value="xor">xor</option>
                <option value="multiply">multiply</option>
                <option value="screen">screen</option>
                <option value="overlay">overlay</option>
                <option value="darken">darken</option>
                <option value="lighten">lighten</option>
                <option value="color-dodge">color-dodge</option>
                <option value="color-burn">color-burn</option>
                <option value="hard-light">hard-light</option>
                <option value="soft-light">soft-light</option>
                <option value="difference">difference</option>
                <option value="exclusion">exclusion</option>
                <option value="hue">hue</option>
                <option value="saturation">saturation</option>
                <option value="color">color</option>
                <option value="luminosity">luminosity</option>
              </select>
            </div>

          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updateContextAttribute()">Save</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import IconEdit from '@/components/icons/IconEdit.vue'
import IconReset from '@/components/icons/IconReset.vue'

export default {
  name: 'ContextPanel',
  components: {
    IconEdit, IconReset
  },
  props: {
  },
  data() {
    return {
      currentAttribute: null,
      newCtx: {
        fillStyle: null,
        lineCap: 'butt',
        lineJoin: 'miter',
        textAlign: 'start',
        textBaseline: 'alphabetic',
        globalCompositeOperation: 'source-over'
      }
    }
  },
  methods: {
    editContextAttribute(attribute) {
      this.currentAttribute = attribute;
      this.loadContextAttributeValues();
    },
    loadContextAttributeValues() {
      this.newCtx.fillStyle = window.ctx.fillStyle;
      this.newCtx.strokeStyle = window.ctx.strokeStyle;
      this.newCtx.font = window.ctx.font;
      this.newCtx.globalAlpha = window.ctx.globalAlpha;
      this.newCtx.lineWidth = window.ctx.lineWidth;
      this.newCtx.lineCap = window.ctx.lineCap;
      this.newCtx.lineJoin = window.ctx.lineJoin;
      this.newCtx.filter = window.ctx.filter;
      this.newCtx.shadowBlur = window.ctx.shadowBlur;
      this.newCtx.shadowColor = window.ctx.shadowColor;
      this.newCtx.shadowOffsetX = window.ctx.shadowOffsetX;
      this.newCtx.shadowOffsetY = window.ctx.shadowOffsetY;
      this.newCtx.lineDashOffset = window.ctx.lineDashOffset;
      this.newCtx.textAlign = window.ctx.textAlign;
      this.newCtx.miterLimit = window.ctx.miterLimit;
      this.newCtx.textBaseline = window.ctx.textBaseline;
      this.newCtx.globalCompositeOperation = window.ctx.globalCompositeOperation;

      this.newCtx.lineDashStr = "[" + window.ctx.getLineDash().toString() + "]";
    },
    updateContextAttribute() {
      if (this.currentAttribute=='fillStyle') { window.ctx.fillStyle = this.newCtx.fillStyle; }
      else if (this.currentAttribute=='strokeStyle') { window.ctx.strokeStyle = this.newCtx.strokeStyle; }
      else if (this.currentAttribute=='font') { window.ctx.font = this.newCtx.font; }
      else if (this.currentAttribute=='globalAlpha') { window.ctx.globalAlpha = this.newCtx.globalAlpha; }
      else if (this.currentAttribute=='lineWidth') { window.ctx.lineWidth = this.newCtx.lineWidth; }
      else if (this.currentAttribute=='lineCap') { window.ctx.lineCap = this.newCtx.lineCap; }
      else if (this.currentAttribute=='lineJoin') { window.ctx.lineJoin = this.newCtx.lineJoin; }
      else if (this.currentAttribute=='filter') { window.ctx.filter = this.newCtx.filter; }
      else if (this.currentAttribute=='shadowBlur') { window.ctx.shadowBlur = this.newCtx.shadowBlur; }
      else if (this.currentAttribute=='shadowColor') { window.ctx.shadowColor = this.newCtx.shadowColor; }
      else if (this.currentAttribute=='shadowOffsetX') { window.ctx.shadowOffsetX = this.newCtx.shadowOffsetX; }
      else if (this.currentAttribute=='shadowOffsetY') { window.ctx.shadowOffsetY = this.newCtx.shadowOffsetY; }
      else if (this.currentAttribute=='lineDashOffset') { window.ctx.lineDashOffset = this.newCtx.lineDashOffset; }
      else if (this.currentAttribute=='textAlign') { window.ctx.textAlign = this.newCtx.textAlign; }
      else if (this.currentAttribute=='miterLimit') { window.ctx.miterLimit = this.newCtx.miterLimit; }
      else if (this.currentAttribute=='textBaseline') { window.ctx.textBaseline = this.newCtx.textBaseline; }
      else if (this.currentAttribute=='globalCompositeOperation') { window.ctx.globalCompositeOperation = this.newCtx.globalCompositeOperation; }
      
      else if (this.currentAttribute=='lineDash') {
        window.ctx.setLineDash(this.newCtx.lineDashStr.replaceAll("[","").replaceAll("]","").split(","));
      }

      this.$parent.updateCurrentContextDetails();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
