<template>
  <div>
    <div>
        <div class="panelHeader" v-on:click="$parent.togglePanelVisibility('layers')">
            <span class="panelTitle">Layers</span>
        </div>
        <div v-show="$parent.visiblePanels.layers" class="panelBody">
            <table class="smallTable">
                <thead>
                    <tr>
                        <th>visible</th>
                        <th>name</th>
                        <th>alpha</th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="layer in $parent.canvasLayers" v-on:click="$parent.setCurrentLayer(layer.name)"  v-bind:key="layer.name" v-bind:class="{'selectedLayer': ($parent.currentLayer != null && layer.name == $parent.currentLayer.name)}">
                        <td>
                            <div class="custom-control custom-switch" style="text-align: center;">
                                <input type="checkbox" v-model="layer.visible" v-on:change="$parent.updateCanvasAllLayers()" class="custom-control-input" :id="'_layer_visible_' + layer.name">
                                <label class="custom-control-label" :for="'_layer_visible_' + layer.name"></label>
                            </div>
                        </td>
                        <td><a>{{ layer.name }}</a></td>
                        <td>{{ layer.alpha }}</td>
                        <td><button v-on:click="$parent.setCurrentLayer(layer.name)" class="btn btn-primary btn-sm">select</button></td>
                        <!--<td><button v-on:click="renderLayer(layer.name)" class="btn btn-primary btn-sm">render</button></td>-->
                        <td><button v-on:click="editLayer(layer)" class="btn btn-primary btn-sm" data-toggle="modal" data-target="#modalEditLayer">edit</button></td>
                        <td><button v-on:click="$parent.deleteLayer(layer)" class="btn btn-danger btn-sm">delete</button></td>
                    </tr>
                </tbody>
            </table>
            <div class="buttonGroup">
                <button v-on:click="showAddLayerForm()" data-toggle="modal" data-target="#modalAddLayer" class="btn btn-primary btn-sm">Add layer</button>
                <!--<button v-on:click="setBackgroundLayer()" class="btn btn-primary btn-sm">setBackgroundLayer</button>-->
                <!--<button v-on:click="renderLayers()" class="btn btn-primary btn-sm">renderLayers</button>-->
                <button v-on:click="showDuplicateLayerForm()" data-toggle="modal" data-target="#modalDuplicateLayer" class="btn btn-primary btn-sm">Duplicate</button>
                <button v-on:click="$parent.moveLayerUp()" class="btn btn-primary btn-sm"><IconUp></IconUp></button>
                <button v-on:click="$parent.moveLayerDown()" class="btn btn-primary btn-sm"><IconDown></IconDown></button>
            </div>
        </div>
    </div>


    <!-- Modal EditLayer-->
    <div class="modal fade" id="modalEditLayer" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="modalLabel">Edit layer</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>name: <input v-model="editedLayer.name"></div>
            <div>alpha: <input v-model="editedLayer.alpha"></div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updateLayer()">Save</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal AddLayer -->
    <div class="modal fade" id="modalAddLayer" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="modalLabel">Add layer</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>name: <input v-model="newLayerForm.name"></div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="addLayer()">Save</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal DuplicateLayer -->
    <div class="modal fade" id="modalDuplicateLayer" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="modalLabel">Duplicate layer</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>New layer name: <input v-model="duplicateLayerForm.newLayerName"></div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="duplicateCurrentLayer()">Save</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import IconUp from '@/components/icons/IconUp.vue'
import IconDown from '@/components/icons/IconDown.vue'

export default {
  name: 'LayersPanel',
  components: {
    IconUp, IconDown
  },
  props: {
  },
  data() {
    return {
      originalLayer: null,
      editedLayer: {
        name: null,
        alpha: null
      },
      newLayerForm: {
        name: null
      },
      duplicateLayerForm: {
        newLayerName: null
      }
    }
  },
  methods: {
    showAddLayerForm() {
      this.newLayerForm.name = "NewLayer";
      window.$('#modalAddLayer').modal('show');
    },
    addLayer() {
      this.$parent.addLayerWithName(this.newLayerForm.name);
      this.$parent.setCurrentLayer(this.newLayerForm.name);
    },
    showDuplicateLayerForm() {
      this.duplicateLayerForm.newLayerName = "LayerCopy";
      window.$('#modalDuplicateLayer').modal('show');
    },
    duplicateCurrentLayer() {
      this.$parent.duplicateLayer(this.$parent.currentLayer, this.duplicateLayerForm.newLayerName);
    },
    editLayer(layer) {
      this.originalLayer = layer;

      this.editedLayer.name = this.originalLayer.name;
      this.editedLayer.alpha = this.originalLayer.alpha;
    },
    updateLayer() {
      this.originalLayer.name = this.editedLayer.name;
      this.originalLayer.alpha = parseFloat(this.editedLayer.alpha);

      this.$parent.updateCurrentLayerCanvas();
      this.$parent.updateCanvasAllLayers();
    }
  }
}
</script>

