<template>
    <div v-if="currentScript != null">
        <div class="panelHeader" v-on:click="$parent.togglePanelVisibility('configureScript')">
            <span class="panelTitle">Configure script params</span>
            <a v-on:click="currentScript=null" style="float:right; margin-right: 5px;"><IconClose style="margin-top:-5px;"></IconClose></a>
        </div>
        <div v-if="$parent.visiblePanels.configureScript" class="panelBody">
            <h3>{{currentScript.name }}</h3>
            
            <div v-if="currentScript.params.length > 0"><b>Script params:</b></div>

            <div class="spacing"></div>

            <div v-if="currentScript.params.length == 0">This script has no params.</div>

            <table v-if="currentScript.params.length > 0" class="smallTable">
                <thead>
                    <tr>
                        <th>name</th>
                        <th>type</th>
                        <th>default value</th>
                        <th>edit</th>
                        <th>delete</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="param in currentScript.params" v-bind:key="param.name"> <!-- TODO: substituir v-bind:key="param.name" por v-bind:key="param.id" -->
                        <td>{{param.name}}</td>
                        <td>{{param.type}}</td>
                        <td>{{param.defaultValue}}</td>
                        <td><button v-on:click="editScriptParam(param)" class="btn btn-primary btn-sm">edit</button></td>
                        <td><button v-on:click="deleteScriptParam(param.name)" class="btn btn-danger btn-sm">delete</button></td>
                    </tr>
                </tbody>
            </table>

            <div class="spacing"></div>

            <button v-on:click="showModalAddScriptParam()" class="btn btn-primary btn-sm">Add script param</button>

            <div class="spacing"></div>
        </div>

        <ModalAddScriptParam ref="modalAddScriptParam"></ModalAddScriptParam>
    </div>
</template>

<script>
import ModalAddScriptParam from '@/components/modals/ModalAddScriptParam.vue'
import IconClose from '@/components/icons/IconClose.vue'

export default {
  name: 'ConfigureScriptPanel',
  components: {
    ModalAddScriptParam, IconClose
  },
  props: {
  },
  data() {
    return {
        currentScript: null
    }
  },
  methods: {
    setCurrentScript(script) {
        this.currentScript = script
    },
    editScriptParam(param) {
        this.$refs.modalAddScriptParam.editScriptParam(param);
    },
    showModalAddScriptParam() {
        this.$refs.modalAddScriptParam.open();
        //window.$('#modalAddScriptParam').modal('show');
    },
    addScriptParam(param) {
        if (this.currentScript.params == null) {
            this.currentScript.params = [];
        }
        this.currentScript.params.push(param);
    },
    deleteScriptParam(paramName) {
        let params = this.currentScript.params;
        for (let i=0; i<params.length; i++) {
            let param = params[i];
            if (param.name == paramName) {
                params.splice(i, 1);
                return;
            }
        }
    }
  }
}
</script>

