<template>
  <div>
    <div class="header">
        <MainMenu></MainMenu>
        <!--<a href="#" class="btn btn-primary" style="position:absolute;left:10px;top:13px;" role="button"><IconRectangle color="#ffffff"></IconRectangle> Menu</a>-->
        <img src="../assets/drawjs-logo.png">
        <a href="https://danielbarral.itch.io/drawjs/donate" class="btn btn-danger" style="position:absolute;right:10px;top:13px;" role="button" target="top"><IconDonate color="#ffffff"></IconDonate> Donate</a>
    </div>

    <div id="app" class="appDiv">

        <div class="mainLayout">

            <!--<div class="remainingSpacing"></div>-->
            <div class="columnBox colDiv">

                <div class="scriptsDiv">
                    <div>
                        <ul class="nav nav-tabs" id="myTab" role="tablist">
                            <li class="nav-item">
                                <a class="nav-link active" id="sampleScripts-tab" data-toggle="tab" href="#tabSampleScripts" role="tab">Sample scripts</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" id="savedScripts-tab" data-toggle="tab" href="#tabSavedScripts" role="tab">My scripts</a>
                            </li>
                        </ul>
                    </div>
                    <div class="tabContentScroll">
                        <div class="tab-content" id="myTabContent">
                            <div class="tab-pane show active" id="tabSampleScripts" role="tabpanel" aria-labelledby="sampleScripts-tab">
                                <div v-show="selectedSampleScript == null">
                                    <span>Type:</span>
                                    <select v-model="sampleScriptsFilter.type" v-on:change="filterSampleScripts()">
                                        <!--<option disabled value="">Type</option>-->
                                        <option value="">(Show all)</option>
                                        <option v-for="type in sampleScriptTypes" v-bind:value="type" v-bind:key="type">
                                            {{ type }}
                                        </option>
                                    </select>
                                    <span>Name:</span>
                                    <input v-model="sampleScriptsFilter.text" v-on:input="filterSampleScripts()" placeholder="script name">
                                    <div v-show="sampleScriptsFilter.type != '' || sampleScriptsFilter.text != ''">Showing {{ numFilteredSampleScripts }} of {{numSampleScripts}} <a v-on:click="clearSearch()" href="#">Clear search</a></div>
                                    <table style="margin-top: 5px">
                                        <tr v-for="script in sampleScripts" v-bind:key="script.name">
                                            <td>{{ script.name }}</td>
                                            <td><button v-on:click="executeSampleScript(script.name)" class="btn btn-success btn-sm" style="white-space:nowrap"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute></button></td>
                                            <td>
                                                <button v-on:click="selectSampleScriptWithParams(script.name)" :disabled="script.params == null" class="btn btn-success btn-sm" style="white-space:nowrap">
                                                    <IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute>
                                                    ...
                                                </button>
                                            </td>
                                            <td><button v-on:click="loadSampleScript(script.name)" class="btn btn-primary btn-sm">Open</button></td>
                                            <td><button v-on:click="selectSampleScript(script.name)" class="btn btn-primary btn-sm">View</button></td>
                                        </tr>
                                    </table>
                                </div>
                                <div v-if="selectedSampleScript != null">
                                    <h3><button v-on:click="unselectSampleScript()" class="btn btn-secondary btn-sm"><IconBack color="#ffffff" style="margin-top:-1px;"></IconBack> Back</button> {{ selectedSampleScript.name }}</h3>
                                    <div class="buttonGroup">
                                        <button v-on:click="executeEditedSampleScript()" class="btn btn-success btn-sm"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute> Execute</button>
                                        <button v-on:click="copySampleSourceCode()" class="btn btn-primary btn-sm">Copy</button>
                                        <button v-on:click="showModalSaveSampleScriptAs()" class="btn btn-primary btn-sm">Save as...</button>
                                    </div>
                                    <!--<pre id="selectedSampleScriptSourceCode">{{ selectedSampleScript.source }}</pre>-->
                                    <textarea spellcheck="false" v-model="editedSampleScript" style="width: 100%; height: 180px;"></textarea>
                                </div>
                                <!--
                                <div v-if="selectedSampleScriptWithParams != null">
                                    <h3><button v-on:click="unselectSampleScriptWithParams()" class="btn btn-secondary btn-sm"><IconBack color="#ffffff" style="margin-top:-1px;"></IconBack> Back</button> {{ selectedSampleScriptWithParams.name }}</h3>
                                    <br>
                                    <table>
                                        <tbody>
                                            <tr v-for="param in selectedSampleScriptWithParams.params" v-bind:key="param.name">
                                                <td>{{param.name}}</td>
                                                <td><input v-model="param.value" v-on:input="onSampleScriptParamChange()"></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                    <br>
                                    <div class="buttonGroup">
                                        <button v-on:click="executeSampleScriptWithParams()" class="btn btn-success btn-sm"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute> Execute</button>
                                        <button v-on:click="unselectSampleScriptWithParams()" class="btn btn-secondary btn-sm">Cancel</button>
                                    </div>
                                    <br>
                                    <input type="checkbox" v-model="clearLayerBeforeExecutingScriptWithParams" id="checkbox01">
                                    <label for="checkbox01">Clear layer before executing</label>
                                    <br>
                                    <input type="checkbox" v-model="executeScriptsWithParamsOnChange" id="checkbox02">
                                    <label for="checkbox02">Execute automatically on param change</label>
                                </div>
                                -->
                            </div>
                            <div class="tab-pane" style="margin-left:3px;" id="tabSavedScripts" role="tabpanel" aria-labelledby="savedScripts-tab">
                                <!--<h3>Saved scripts:</h3>-->
                                <div class="buttonGroup">
                                    <button v-on:click="createNewScript()" class="btn btn-primary btn-sm">New script...</button>
                                </div>
                                <br>
                                <table class="smallTable"  v-if="savedScripts.length > 0">
                                    <thead>
                                        <tr>
                                            <th>name</th>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="savedScript in savedScripts" v-bind:key="savedScript.name">
                                            <td><b>{{ savedScript.name }}</b></td>
                                            <td><button v-on:click="executeSavedScript(savedScript.name)" class="btn btn-success btn-sm"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute></button></td>
                                            <td><button v-on:click="selectSavedScriptWithParams(savedScript.name)" :disabled="savedScript.params == null" class="btn btn-success btn-sm" style="white-space:nowrap"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute> ...</button></td>
                                            <td><button v-on:click="configScript(savedScript.name)" class="btn btn-primary btn-sm">Params</button></td>
                                            <td><button v-on:click="loadSavedScript(savedScript.name)" class="btn btn-primary btn-sm">Open</button></td>
                                            <td><button v-on:click="deleteSavedScript(savedScript.name)" class="btn btn-danger btn-sm">delete</button></td>
                                        </tr>
                                    </tbody>
                                </table>
                                <div v-if="savedScripts.length == 0">You haven't created any script yet.</div>
                                <br>
                                <div class="buttonGroup">
                                    <button v-on:click="saveScriptsToLocalStorage()" class="btn btn-primary btn-sm">Save to local storage</button>
                                    <button v-on:click="loadScriptsFromLocalStorage()" class="btn btn-primary btn-sm">Load from local storage</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div>
                    <div class="panelHeader">
                        <span v-show="currentScriptType == 'SAMPLE_SCRIPT'"><b>Sample script:</b> {{ currentScriptName }}</span>
                        <span v-show="currentScriptType == 'SAVED_SCRIPT'"><b>My script:</b> {{ currentScriptName }}</span>
                        <span v-show="currentScriptType == null"><b>Untitled script</b></span>
                    </div>
                    <textarea id="sourceCode" style="width: 100%; height: 240px;" spellcheck="false">
                    </textarea>
                    <div class="buttonGroup">
                        <button v-on:click="executeScript" class="btn btn-success btn-sm"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute> Execute</button>
                        <button v-on:click="selectSavedScriptWithParams(currentScriptName)" v-show="currentScriptType == 'SAVED_SCRIPT'" class="btn btn-success btn-sm" style="white-space:nowrap"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute> ...</button>
                        <button v-on:click="selectSampleScriptWithParams(currentScriptName)" v-show="currentScriptType == 'SAMPLE_SCRIPT'" class="btn btn-success btn-sm" style="white-space:nowrap"><IconExecute color="#ffffff" style="margin-top:-1px;"></IconExecute> ...</button>
                        <button v-on:click="configScript(currentScriptName)" class="btn btn-primary btn-sm" :disabled="currentScriptType != 'SAVED_SCRIPT'">Params</button>
                        <button v-on:click="saveScript" class="btn btn-primary btn-sm" :disabled="currentScriptType != 'SAVED_SCRIPT'">Save</button>
                        <button v-on:click="saveScriptAs" class="btn btn-primary btn-sm">Save as...</button>
                        <button v-on:click="newScript" class="btn btn-primary btn-sm">New</button>
                    </div>
                </div> 

            </div>
            <div class="columnBox mainColDiv">

                <ul class="nav nav-tabs" id="myTab" role="tablist">
                    <!--
                    <li class="nav-item">
                        <a class="nav-link active" id="profile-tab" data-toggle="tab" href="#tab_main_canvas" role="tab" aria-controls="tab_main_canvas" aria-selected="false">Main canvas</a>
                    </li>
                    -->
                    <li class="nav-item">
                        <a class="nav-link active" id="contact-tab" data-toggle="tab" href="#tab_all_layers" role="tab" aria-controls="tab_all_layers" aria-selected="false">All layers</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" id="contact-tab" data-toggle="tab" href="#tab_current_layer" role="tab" aria-controls="tab_current_layer" aria-selected="false">Current layer</a>
                    </li>
                </ul>
                <div class="tab-content" id="myTabContent" style="margin-top: 5px;">
                    <!--
                    <div class="tab-pane show active" id="tab_main_canvas" role="tabpanel" aria-labelledby="profile-tab">
                        <canvas id="canvas" width="600" height="400" style="border:1px solid #000000;">
                        </canvas>
                    </div>
                    -->
                    <div class="tab-pane show active" id="tab_all_layers" role="tabpanel" aria-labelledby="contact-tab">
                        <canvas id="canvasAllLayers" width="600" height="400" style="border:1px solid #000000;">
                        </canvas>
                    </div>
                    <div class="tab-pane" id="tab_current_layer" role="tabpanel" aria-labelledby="contact-tab">
                        <canvas id="canvasCurrentLayer" width="600" height="400" style="border:1px solid #000000;">
                        </canvas>
                    </div>
                </div>

                <div class="buttonGroup">
                    <span><b>Current tool: </b>{{currentTool}}</span>

                    <span style="margin-left:10px"><b>fillStyle: </b></span>
                    <span class="colorSquare" :style="'backgroundColor:' + ctx.fillStyle"></span>
                    <span class="spanFillStyleStrokeStyle">{{ ctx.fillStyle }}</span>

                    <span style="margin-left:10px"><b>strokeStyle: </b></span>
                    <span class="colorSquare" :style="'backgroundColor:' + ctx.strokeStyle"></span>
                    <span class="spanFillStyleStrokeStyle">{{ ctx.strokeStyle }}</span>

                    <span style="margin-left:10px"><b>Position: </b>{{ mousePosition.x }},{{ mousePosition.y }}</span>
                </div>

                <div class="buttonGroup">
                    <button v-on:click="setCurrentTool('pencil')" class="btn btn-primary btn-sm"><IconEdit color="#ffffff"></IconEdit> Pencil</button>
                    <button v-on:click="setCurrentTool('brush')" class="btn btn-primary btn-sm"><IconBrush color="#ffffff"></IconBrush> Brush</button>
                    <button v-on:click="setCurrentTool('line')" class="btn btn-primary btn-sm"><IconLine color="#ffffff"></IconLine> Line</button>
                    <button v-on:click="setCurrentTool('rectangle')" class="btn btn-primary btn-sm"><IconRectangle color="#ffffff"></IconRectangle> Rectangle</button>
                    <button v-on:click="setCurrentTool('circle')" class="btn btn-primary btn-sm"><IconCircle color="#ffffff"></IconCircle> Circle</button>
                    <button v-on:click="setCurrentTool('ellipse')" class="btn btn-primary btn-sm"><IconEllipse color="#ffffff"></IconEllipse> Ellipse</button>
                    <button v-on:click="setCurrentTool('none')" class="btn btn-primary btn-sm"><IconNone color="#ffffff"></IconNone> None</button>
                </div>

                <div class="buttonGroup">
                    <!--<button v-on:click="clearCanvas()" class="btn btn-primary btn-sm">Clear main canvas</button>-->
                    <button v-on:click="clearCurrentLayer()" class="btn btn-primary btn-sm">Clear current layer</button>
                    <button v-on:click="fillScreenWhite()" class="btn btn-primary btn-sm">Fill with white</button>
                    <button v-on:click="fillScreenBlack()" class="btn btn-primary btn-sm">Fill with black</button>
                    <button v-on:click="repaintCurrentLayer()" class="btn btn-primary btn-sm">Repaint current layer</button>
                    <!--<button v-on:click="showExportModal()" data-toggle="modal" data-target="#modalExport" class="btn btn-primary btn-sm">Export...</button>-->
                    <button v-on:click="clip()" class="btn btn-primary btn-sm">Clip</button>
                </div>

                

            </div>
            <div class="columnBoxRight">
                <ConfigureScriptPanel ref="configureScriptPanel"></ConfigureScriptPanel>
                <ScriptWithParamsPanel></ScriptWithParamsPanel>
                <LayersPanel ref="layersPanel"></LayersPanel>
                <ContextPanel></ContextPanel>
                <EventsPanel></EventsPanel>
                <ImagesPanel></ImagesPanel>
                <FontsPanel></FontsPanel>
                <GlobalVariablesPanel></GlobalVariablesPanel>
                <LogsPanel></LogsPanel>
            </div>
        </div> <!-- mainLayout -->


        <!-- Modal Save sample script as -->
        <div class="modal fade" id="modalSaveSampleScriptAs" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="modalLabel">Save sample script as...</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div>Name: <input v-model="newScriptName"></div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-primary" v-on:click="saveSampleScriptAs()">Save</button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
                </div>
                </div>
            </div>
        </div>


        <ModalExport></ModalExport>


    </div> <!-- appDiv -->
  </div>
</template>

<script>

import {getSampleScripts, getSampleScriptTypes} from '@/lib/sampleScripts.js'
import $util from '@/lib/util.js'

import MainMenu from '@/components/MainMenu.vue'

import ContextPanel from '@/components/panels/ContextPanel.vue'
import LayersPanel from '@/components/panels/LayersPanel.vue'
import EventsPanel from '@/components/panels/EventsPanel.vue'
import ImagesPanel from '@/components/panels/ImagesPanel.vue'
import FontsPanel from '@/components/panels/FontsPanel.vue'
import GlobalVariablesPanel from '@/components/panels/GlobalVariablesPanel.vue'
import LogsPanel from '@/components/panels/LogsPanel.vue'
import ScriptWithParamsPanel from '@/components/panels/ScriptWithParamsPanel.vue'
import ConfigureScriptPanel from '@/components/panels/ConfigureScriptPanel.vue'

import ModalExport from '@/components/modals/ModalExport.vue'

//import IconUp from '@/components/icons/IconUp.vue'
//import IconDown from '@/components/icons/IconDown.vue'
//import IconReset from '@/components/icons/IconReset.vue'
import IconEdit from '@/components/icons/IconEdit.vue'
import IconBrush from '@/components/icons/IconBrush.vue'
import IconCircle from '@/components/icons/IconCircle.vue'
import IconRectangle from '@/components/icons/IconRectangle.vue'
import IconLine from '@/components/icons/IconLine.vue'
import IconEllipse from '@/components/icons/IconEllipse.vue'
import IconNone from '@/components/icons/IconNone.vue'
import IconExecute from '@/components/icons/IconExecute.vue'
import IconDonate from '@/components/icons/IconDonate.vue'
import IconBack from '@/components/icons/IconBack.vue'
//import IconSettings from '@/components/icons/IconSettings.vue'

export default {
  name: 'Main',
  components: {
    MainMenu,

    IconEdit, IconBrush, IconCircle, IconRectangle, IconLine, IconEllipse, IconNone, IconExecute, IconDonate,
    IconBack, //IconSettings,

    ModalExport,

    ContextPanel, LayersPanel, EventsPanel, ImagesPanel, FontsPanel, GlobalVariablesPanel, LogsPanel, ScriptWithParamsPanel,
    ConfigureScriptPanel
  },
  methods: {
    loadFont() {
        if (!this.fontPanelForm.fontName) {
            this.fontPanelForm.fontName = "font01";
        }
        let url = this.fontPanelForm.fontURL;
        let urlString = `url(${url})`;
        let f = new FontFace(this.fontPanelForm.fontName, urlString);
        let that = this;
        f.load().then(function(font) {
            document.fonts.add(font);
            let loadedFont = {
                "name": font.family,
                "font": font
            };
            that.fonts.push(loadedFont);
        });
    },
    onFontUpload(e) {
        const file = e.target.files[0];
        let url = URL.createObjectURL(file);
        let urlString = `url(${url})`;
        if (!this.fontPanelForm.fontName) {
            this.fontPanelForm.fontName = "font01";
        }
        var f = new FontFace(this.fontPanelForm.fontName, urlString);
        var that = this;
        f.load().then(function(font) {
            document.fonts.add(font);
            let loadedFont = {
                "name": font.family,
                "font": font
            };
            that.fonts.push(loadedFont);
        });
    },
    setCurrentTool(tool) {
        this.currentTool = tool;
        if (tool == 'pencil') {
            window.ctx.lineWidth = 3;
            window.ctx.filter = 'none';
        } else if (tool == 'brush') {
            window.ctx.lineWidth = 20;
            window.ctx.filter = 'blur(3px)';
        }
        this.updateCurrentContextDetails();
    },
    onCanvasMouseUp(event) { // eslint-disable-line no-unused-vars
        this.isMouseDown = false;

        let canvasChanged = false;

        if (this.currentTool == 'line') {
            this.drawLine();
            canvasChanged = true;
        } else if (this.currentTool == 'rectangle') {
            this.drawRect();
            canvasChanged = true;
        } else if (this.currentTool == 'circle') {
            this.drawCircle();
            canvasChanged = true;
        } else if (this.currentTool == 'ellipse') {
            this.drawEllipse();
            canvasChanged = true;
        }

        for (let i=0; i<this.customEvents.length; i++) {
            let customEvent = this.customEvents[i];
            if (customEvent.type=="mouseUp" && customEvent.enabled) {
                customEvent.func();
                canvasChanged = true;
            }
        }

        if (canvasChanged) {
            this.updateCanvasAllLayers();
            this.updateCurrentLayerCanvas();
        }
    },
    onCanvasMouseDown(event) { // eslint-disable-line no-unused-vars
        this.isMouseDown = true;
        this.updateMouseDownPosition(event);
        let canvasChanged = false;
        if (this.currentTool == 'pencil' || this.currentTool == 'brush') {
            this.paintBrush();
            canvasChanged = true;
        }
        for (let i=0; i<this.customEvents.length; i++) {
            let customEvent = this.customEvents[i];
            if (customEvent.type=="mouseDown" && customEvent.enabled) {
                customEvent.func();
                canvasChanged = true;
            }
        }
        if (canvasChanged) {
            this.updateCanvasAllLayers();
            this.updateCurrentLayerCanvas();
        }
    },
    onCanvasMouseMove(event) {
        this.updateMousePosition(event);
        if (this.isMouseDown) {
            if (this.currentTool == 'pencil' || this.currentTool == 'brush') {
                this.paintBrush();
            }
        }
        for (let i=0; i<this.customEvents.length; i++) {
            let customEvent = this.customEvents[i];
            if (customEvent.type=="mouseMove" && customEvent.enabled) {
                customEvent.func();
            }
        }
        this.updateCanvasAllLayers();
        this.updateCurrentLayerCanvas();
    },
    drawLine() {
        let ctx = window.ctx;
        ctx.beginPath();
        ctx.moveTo(this.mouseDownPosition.x, this.mouseDownPosition.y);
        ctx.lineTo(this.mousePosition.x, this.mousePosition.y);
        ctx.stroke();
    },
    drawRect() {
        let ctx = window.ctx;
        ctx.beginPath();
        ctx.rect(this.mouseDownPosition.x, this.mouseDownPosition.y, this.mousePosition.x - this.mouseDownPosition.x, this.mousePosition.y - this.mouseDownPosition.y);
        ctx.stroke();
    },
    drawCircle() {
        let centerX = (this.mousePosition.x + this.mouseDownPosition.x) / 2;
        let centerY = (this.mousePosition.y + this.mouseDownPosition.y) / 2;
        let dx = Math.abs(this.mousePosition.x - this.mouseDownPosition.x);
        let dy = Math.abs(this.mousePosition.y - this.mouseDownPosition.y);
        let radius = Math.min(dx, dy) / 2;

        let ctx = window.ctx;
        ctx.beginPath();
        ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
        ctx.stroke();
    },
    drawEllipse() {
        let x = (this.mousePosition.x + this.mouseDownPosition.x) / 2;
        let y = (this.mousePosition.y + this.mouseDownPosition.y) / 2;
        let radiusX = Math.abs(this.mousePosition.x - this.mouseDownPosition.x) / 2;
        let radiusY = Math.abs(this.mousePosition.y - this.mouseDownPosition.y) / 2;
        let rotation = 0;
        let startAngle = 0;
        let endAngle = 2 * Math.PI;
        let anticlockwise = false;

        let ctx = window.ctx;
        ctx.beginPath()
        ctx.ellipse(x, y, radiusX, radiusY, rotation, startAngle, endAngle, anticlockwise);
        ctx.stroke();
    },
    paintBrush() {
        //let currentLayerCtx = this.currentLayer.canvas.getContext('2d');
        ctx.save(); // eslint-disable-line no-undef
        ctx.beginPath(); // eslint-disable-line no-undef
        ctx.lineCap = 'round'; // eslint-disable-line no-undef
        ctx.moveTo(this.previousMousePosition.x, this.previousMousePosition.y); // eslint-disable-line no-undef
        ctx.lineTo(this.mousePosition.x, this.mousePosition.y); // eslint-disable-line no-undef
        ctx.stroke(); // eslint-disable-line no-undef
        ctx.restore(); // eslint-disable-line no-undef
    },
    updateMousePosition(event) {
        let canvas = null;
        if (event.target.id=="canvasAllLayers") {
            canvas = document.getElementById('canvasAllLayers');
        } else if (event.target.id=="canvasCurrentLayer") {
            canvas = document.getElementById('canvasCurrentLayer');
        }
        this.previousMousePosition.x = this.mousePosition.x;
        this.previousMousePosition.y = this.mousePosition.y;
        this.mousePosition.x = event.clientX - canvas.offsetLeft; 
        this.mousePosition.y = event.clientY - canvas.offsetTop; 
    },
    updateMouseDownPosition(event) {
        let canvas = null;
        if (event.target.id=="canvasAllLayers") {
            canvas = document.getElementById('canvasAllLayers');
        } else if (event.target.id=="canvasCurrentLayer") {
            canvas = document.getElementById('canvasCurrentLayer');
        }
        this.mouseDownPosition.x = event.clientX - canvas.offsetLeft; 
        this.mouseDownPosition.y = event.clientY - canvas.offsetTop; 
    },
    editGlobalVariable(variableName) {
        let value = prompt('New value', '');
        let type = prompt('New type', 'number');

        if (type == 'number') {
            value = Number(value);
        }

        this.globalVariables[variableName] = value;

        this.$forceUpdate();
    },
    deleteGlobalVariable(name) {
        delete this.globalVariables[name];
        this.$forceUpdate();
    },
    popupAddGlobalVariable() {
        let variableName = prompt('Variable name', '');
        let type = prompt('Type', 'number');
        let value = prompt('Value', '');

        if (type == 'number') {
            value = Number(value);
        }

        this.addGlobalVariable(variableName, value);

        this.$forceUpdate();
    },
    addGlobalVariable(variableName, value) {
        this.globalVariables[variableName] = value;
    },
    combineLayers(baseLayerName, maskLayerName) {
        let baseLayer = this.getLayerByName(baseLayerName);
        let baseCtx = baseLayer.canvas.getContext('2d');
        let baseImageData = baseCtx.getImageData(0, 0, 600, 400); //TODO: remover hard-coded

        let maskLayer = this.getLayerByName(maskLayerName);
        let maskCtx = maskLayer.canvas.getContext('2d');
        let maskImageData = maskCtx.getImageData(0, 0, 600, 400); //TODO: remover hard-coded

        for (let i = 0; i < baseImageData.data.length; i += 4) {
            baseImageData.data[i+3] = baseImageData.data[i+3] * (maskImageData.data[i+3] / 255);
        }

        baseCtx.putImageData(baseImageData, 0, 0);
    },
    updateCanvasAllLayers() {
