    resetContextAttribute(attribute) {
        let ctx = window.ctx;
        if (attribute=='fillStyle') { ctx.fillStyle = '#000000'; }
        else if (attribute=='strokeStyle') { ctx.strokeStyle = '#000000'; }
        else if (attribute=='font') { ctx.font = '10px sans-serif'; }
        else if (attribute=='globalAlpha') { ctx.globalAlpha = '1'; }
        else if (attribute=='lineWidth') { ctx.lineWidth = '1'; }
        else if (attribute=='lineCap') { ctx.lineCap = 'butt'; }
        else if (attribute=='lineJoin') { ctx.lineJoin = 'miter'; }
        else if (attribute=='filter') { ctx.filter = 'none'; }
        else if (attribute=='shadowBlur') { ctx.shadowBlur = '0'; }
        else if (attribute=='shadowColor') { ctx.shadowColor = 'rgba(0, 0, 0, 0)'; }
        else if (attribute=='shadowOffsetX') { ctx.shadowOffsetX = '0'; }
        else if (attribute=='shadowOffsetY') { ctx.shadowOffsetY = '0'; }
        else if (attribute=='lineDashOffset') { ctx.lineDashOffset = 0; }
        else if (attribute=='textAlign') { ctx.textAlign = 'start'; }
        else if (attribute=='miterLimit') { ctx.miterLimit = 10; }
        else if (attribute=='textBaseline') { ctx.textBaseline = 'alphabetic'; }
        else if (attribute=='globalCompositeOperation') { ctx.globalCompositeOperation = 'source-over'; }

        else if (attribute=='lineDash') { ctx.setLineDash([]); }

        this.updateCurrentContextDetails();
    },
