    buildBackgroundCanvas() {
        let canvas = document.createElement('canvas');
        canvas.width = 600;
        canvas.height = 400;

        let backgroundCtx = canvas.getContext('2d');
        backgroundCtx.clearRect(0, 0, 600, 400); 

        let firstColDark = true;
        let dark = true;

        let tileSize = 10;

        for (let x = 0; x < 600; x += tileSize) {
            dark = firstColDark;
            for (let y = 0; y < 600; y += tileSize) {
                if (dark) {
                    backgroundCtx.fillStyle = "#cccccc";
                } else {
                    backgroundCtx.fillStyle = "#eeeeee";
                }
                backgroundCtx.fillRect(x, y, tileSize, tileSize);
                dark = !dark;
            }
            firstColDark = !firstColDark;
        }

        return canvas;
    },
    addLayerWithName(layerName) {
        let canvas = document.createElement('canvas');
        canvas.width = 600;
        canvas.height = 400;
        canvas.getContext('2d').clearRect(0, 0, 600, 400); 

        let canvasLayer = {
            "name": layerName,
            "alpha": 1,
            "canvas": canvas,
            "visible": true
        };

        this.canvasLayers.push(canvasLayer);
    },
