        if (layer == this.currentLayer) {
            if (this.canvasLayers.length > 0) {
                this.setCurrentLayer(this.canvasLayers[0].name);
            } else {
                //this.setBackgroundLayer();
                //TODO: verificar
            }
        }

        this.updateCanvasAllLayers();
