    setCurrentLayer(name) {
        let layer = this.getLayerByName(name);
        if (layer != null) {
            this.currentLayer = layer;
            //TODO: mudar nome da variável para não confundir com this.ctx
            window.ctx = layer.canvas.getContext('2d'); // eslint-disable-line no-undef
            this.updateCurrentContextDetails();
        }

        this.updateCurrentLayerCanvas();
    },
    getLayerByName(name) {
        for (let i=0; i<this.canvasLayers.length; i++) {
            let layer = this.canvasLayers[i];
            if (layer.name == name) {
                return layer;
            }
        }
        return null;
    },
    getCurrentLayerIndex() {
        for (let i=0; i<this.canvasLayers.length; i++) {
            let layer = this.canvasLayers[i];
            if (layer == this.currentLayer) {
                return i;
            }
        }
        return null;
    },
    getLayerIndex(layer) {
        for (let i=0; i<this.canvasLayers.length; i++) {
            let l = this.canvasLayers[i];
            if (l == layer) {
                return i;
            }
        }
        return null;
    },
    getCustomEventIndex(customEvent) {
        for (let i=0; i<this.customEvents.length; i++) {
            let event = this.customEvents[i];
            if (event.name == customEvent.name) {
                return i;
            }
        }
        return null;
