    getCurrentLayer() {
        return this.currentLayer;
    },
    getCurrentCanvas() {
        return this.getCurrentLayer().canvas;
    },
    getCurrentContext() {
        return this.getCurrentCanvas().getContext('2d');
    },
    updateCurrentContextDetails() {

        let canvas = null;
        if (this.currentLayer == null) {
            //canvas = document.getElementById('canvas');
            return;
        } else {
            canvas = this.currentLayer.canvas;
        }

        this.ctx.fillStyle = canvas.getContext('2d').fillStyle;
        this.ctx.strokeStyle = canvas.getContext('2d').strokeStyle;
        this.ctx.lineWidth = canvas.getContext('2d').lineWidth;
        this.ctx.lineCap = canvas.getContext('2d').lineCap;
        this.ctx.lineJoin = canvas.getContext('2d').lineJoin;
        this.ctx.miterLimit = canvas.getContext('2d').miterLimit;
        this.ctx.font = canvas.getContext('2d').font;
        this.ctx.textAlign = canvas.getContext('2d').textAlign;
        this.ctx.globalAlpha = canvas.getContext('2d').globalAlpha;
        this.ctx.globalCompositeOperation = canvas.getContext('2d').globalCompositeOperation;

        this.ctx.transform = canvas.getContext('2d').getTransform();
        this.ctx.lineDash = canvas.getContext('2d').getLineDash();

        this.ctx.filter = canvas.getContext('2d').filter;
        this.ctx.shadowBlur = canvas.getContext('2d').shadowBlur;
        this.ctx.shadowColor = canvas.getContext('2d').shadowColor;
        this.ctx.shadowOffsetX = canvas.getContext('2d').shadowOffsetX;
        this.ctx.shadowOffsetY = canvas.getContext('2d').shadowOffsetY;
        this.ctx.lineDashOffset = canvas.getContext('2d').lineDashOffset;
        this.ctx.textBaseline = canvas.getContext('2d').textBaseline;
    },
    configScript: function (scriptName) {
        for (let i=0; i<this.savedScripts.length; i++) {
            if (this.savedScripts[i].name == scriptName) {
                let script = this.savedScripts[i];
                this.$refs.configureScriptPanel.setCurrentScript(script);
                this.visiblePanels["configureScript"] = true;
            }
        }
    },
    executeSavedScript: function (scriptName, params) {
        for (let i=0; i<this.savedScripts.length; i++) {
            if (this.savedScripts[i].name == scriptName) {
                if (params == null) {
                    params = {};
                }
                let returnValue = this.savedScripts[i].func(params);
                //this.update();
                //this.renderLayers();
                this.updateCurrentLayerCanvas();
                this.updateCanvasAllLayers();
                this.updateCurrentContextDetails();
                return returnValue;
            }
        }
    },
