    executeSampleScript: function (scriptName, params) {
        for (let i=0; i<this.allSampleScripts.length; i++) {
            if (this.allSampleScripts[i].name == scriptName) {
                if (params == null) {
                    params = {};
                }
                this.allSampleScripts[i].func(params);
            }
        }
        //this.update();
        //this.renderLayers();

        this.updateCurrentLayerCanvas();
        this.updateCanvasAllLayers();
        this.updateCurrentContextDetails();
    },
    executeSampleScriptWithParams: function () {
        
        if (this.clearLayerBeforeExecutingScriptWithParams) {
            this.clearCurrentLayer();
        }

        let params = {};
        if (this.selectedSampleScriptWithParams.params != null) {
            for (let i=0; i<this.selectedSampleScriptWithParams.params.length; i++) {
                let param = this.selectedSampleScriptWithParams.params[i];

                if (param.value == null) {
                    params[param.name] = param.defaultValue;
                } else {
                    if (param.type=="number") {
                        params[param.name] = Number(param.value);
                    } else {
                        params[param.name] = param.value;
                    }
                }
            }
        }
        this.selectedSampleScriptWithParams.func(params);

        this.updateCurrentLayerCanvas();
        this.updateCanvasAllLayers();
        this.updateCurrentContextDetails();
    },
    onSampleScriptParamChange() {
        if (this.executeScriptsWithParamsOnChange) {
            this.executeSampleScriptWithParams();
        }
    },
    deleteSavedScript(scriptName) {
        for (let i=0; i<this.savedScripts.length; i++) {
            if (this.savedScripts[i].name == scriptName) {
                this.savedScripts.splice(i, 1);
            }
        }
    },
