    exportCanvas() {
        if (this.exportConfigForm.type == 'FULL_CANVAS') {
            this.exportCanvasAllLayers();
        } else if (this.exportConfigForm.type == 'CROP') {
            this.exportCanvasAllLayersCropped();
        } else if (this.exportConfigForm.type == 'CROP_AT') {
            this.exportCanvasAllLayersCroppedAt();
        }
    },
    exportCanvasAllLayers() {
        let canvas = document.createElement('canvas');
        canvas.width = 600;
        canvas.height = 400;

        let ctx = canvas.getContext('2d');
        this.drawAllLayers(ctx);

        let dataURL = canvas.toDataURL("image/png");
        window.open(dataURL);
    },
    exportCanvasAllLayersCropped() {
        let originalCanvas = document.createElement('canvas');
        originalCanvas.width = 600;
        originalCanvas.height = 400;

        let ctx = originalCanvas.getContext('2d');
        this.drawAllLayers(ctx);

        let croppedcanvas = $util.crop(originalCanvas); // eslint-disable-line no-undef

        let dataURL = croppedcanvas.toDataURL("image/png");
        window.open(dataURL);
    },
    exportCanvasAllLayersCroppedAt() {
        let originalCanvas = document.createElement('canvas');
        originalCanvas.width = 600;
        originalCanvas.height = 400;

        let ctx = originalCanvas.getContext('2d');
        this.drawAllLayers(ctx);

        let croppedcanvas = $util.cropAt(
            originalCanvas, 
            this.exportConfigForm.cropStartX,
            this.exportConfigForm.cropStartY,
            this.exportConfigForm.cropEndX,
            this.exportConfigForm.cropEndY); // eslint-disable-line no-undef

        let dataURL = croppedcanvas.toDataURL("image/png");
        window.open(dataURL);
    },
    drawAllLayers(ctx) {
        for (let i=this.canvasLayers.length-1; i>=0; i--) {
            let layer = this.canvasLayers[i];
            if (layer.visible) {
                ctx.save();
                ctx.globalAlpha = layer.alpha;
                ctx.drawImage(layer.canvas, 0, 0);
                ctx.restore();
            }
        }
    },
    drawTiledBackground(ctx) {
        ctx.save();
        ctx.globalAlpha = 1;
        ctx.drawImage(this.backgroundCanvas, 0, 0);
        ctx.restore();
    },
    loop() {
        //this.updateMousePosition(event);
        for (let i=0; i<this.customEvents.length; i++) {
            let customEvent = this.customEvents[i];
            if (customEvent.type=="animationFrame" && customEvent.enabled) {
                customEvent.func();
            }
        }
        this.updateCanvasAllLayers();
        this.updateCurrentLayerCanvas();

        window.requestAnimationFrame(this.loop);
    },
    showExportModal() {
        window.$('#modalExport').modal('show');
    },
    showModalSaveSampleScriptAs() {
        this.newScriptName = "";
        window.$('#modalSaveSampleScriptAs').modal('show');
    }
  },
  data() {
    return {
      currentScriptName: null,
      currentScript: null,
      currentScriptType: null, //SAMPLE_SCRIPT ou SAVED_SCRIPT
      newScriptName: "",
      dinamicFunction: new Function("params", ""),
      savedScripts: [],
      sampleScripts: [],
      allSampleScripts: [],
      numSampleScripts: null,
      numFilteredSampleScripts: null,
      selectedSampleScript: null,
      selectedSampleScriptWithParams: null,
      editedSampleScript: "",
      canvasLayers: [],
      backgroundCanvas: null,
      globalVariables: {},
      viewType: "MAIN_CANVAS",
      //currentCanvasLayerIndex: 0,
      currentLayer: null,
      ctx: {
          fillStyle: null,
          strokeStyle: null,
          lineWidth: null,
          lineCap: null,
          lineJoin: null,
          miterLimit: null,
          font: null,
          textAlign: null,
          globalAlpha: null,
          globalCompositeOperation: null,
          transform: null
      },
      sampleScriptsFilter: {
          type: "",
          text: ""
      },
      sampleScriptTypes: [],
      images: [],
      fonts: [],
      visiblePanels: {
          layers: true,
          context: false,
          images: false,
          globalVariables: false,
          fonts: false,
          logs: false,
          events: false,
          scriptWithParams: true,
          configureScript: false
      },
      logs: [],
      mousePosition: {
          x: 0,
          y: 0
      },
      previousMousePosition: {
          x: 0,
          y: 0
      },
      mouseDownPosition: {
          x: 0,
          y: 0
      },
      isMouseDown: false,
      currentTool: "pencil", //pencil, brush, line, rect, circle, none
      customEvents: [],
      fontPanelForm: {
          fontName: '',
          fontURL: ''
      },
      exportConfigForm: {
        type: "FULL_CANVAS",
        cropStartX: 0,
        cropStartY: 0,
        cropEndX: 600,
        cropEndY: 500
      },
      clearLayerBeforeExecutingScriptWithParams: false,
      executeScriptsWithParamsOnChange: false
    }
  },
  mounted () {
    window.app = this;
    window.$util = $util;

    this.allSampleScripts = getSampleScripts();
    this.sampleScripts = getSampleScripts();
    this.sampleScriptTypes = getSampleScriptTypes();

    this.numSampleScripts = this.allSampleScripts.length;
    this.numFilteredSampleScripts = this.allSampleScripts.length;

    this.backgroundCanvas = this.buildBackgroundCanvas();

    let initialLayerName = 'layer01';
    this.addLayerWithName(initialLayerName);
    this.setCurrentLayer(initialLayerName);

    this.updateCanvasAllLayers();
    this.updateCurrentContextDetails();

    document.getElementById('canvasAllLayers').addEventListener('mousemove', this.onCanvasMouseMove);
    document.getElementById('canvasAllLayers').addEventListener('mouseup', this.onCanvasMouseUp);
    document.getElementById('canvasAllLayers').addEventListener('mousedown', this.onCanvasMouseDown);

    document.getElementById('canvasCurrentLayer').addEventListener('mousemove', this.onCanvasMouseMove);
    document.getElementById('canvasCurrentLayer').addEventListener('mouseup', this.onCanvasMouseUp);
    document.getElementById('canvasCurrentLayer').addEventListener('mousedown', this.onCanvasMouseDown);

    window.requestAnimationFrame(this.loop);
  }

}

</script>

