<template>
    <nav style="position: absolute" class="navbar navbar-expand navbar-light">
        <a @mousedown="onMainMenuClick()" href="#" class="btn btn-primary" style="position:absolute;left:10px;top:13px;" role="button" id="navbarDropdownMenuLink">
          <IconMenu color="#ffffff" style="margin-top:-3px;"></IconMenu> <b>Menu</b>
        </a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNavDropdown">
            <ul class="navbar-nav">
                <li class="nav-item dropdown">
                    <ul class="dropdown-menu shadow" id="main-menu-children" aria-labelledby="navbarDropdownMenuLink">

                    <li v-for="menuItem in menuItems" v-bind:key="menuItem.id" :class="menuItem.children.length > 0 ? 'dropdown-submenu' : ''">
                      <a v-if="menuItem.children.length==0" v-on:click="onMenuItemClick(menuItem.id)" @mouseover="mouseOver('submenu-' + menuItem.id)" class="dropdown-item" href="#">{{menuItem.name}}</a>
                      <a v-if="menuItem.children.length>0" v-on:click="onMenuItemClick(menuItem.id)" @mouseover="mouseOver('submenu-' + menuItem.id)" :id="'submenu-' + menuItem.id" class="dropdown-item dropdown-toggle" href="#">{{menuItem.name}}</a>
                      <ul v-if="menuItem.children.length>0" class="dropdown-menu shadow" :id="'submenu-' + menuItem.id + '-children'">
                        <li v-for="child in menuItem.children" v-bind:key="child.id" class="dropdown-submenu">
                            <a v-if="child.children.length==0" v-on:click="onMenuItemClick(child.id)" @mouseover="mouseOverLevel2('subsubmenu-' + child.id)" :id="'subsubmenu-' + child.id" class="dropdown-item" href="#">{{child.name}}</a>
                            <a v-if="child.children.length>0" v-on:click="onMenuItemClick(child.id)" @mouseover="mouseOverLevel2('subsubmenu-' + child.id)" :id="'subsubmenu-' + child.id" class="dropdown-item dropdown-toggle" href="#">{{child.name}}</a>
                            <ul v-if="child.children.length>0" class="dropdown-menu shadow" :id="'subsubmenu-' + child.id + '-children'">
                              <li v-for="child2 in child.children" v-bind:key="child2.id">
                                <a v-on:click="onMenuItemClick(child2.id)" class="dropdown-item" href="#">{{child2.name}}</a>
                              </li>
                            </ul>
                        </li>
                      </ul>
                    </li>

                    </ul>
                </li>
            </ul>
        </div>
    </nav>
</template>

<script>
import IconMenu from '@/components/icons/IconMenu.vue'

export default {
  name: 'MainMenu',
  components: {
    IconMenu
  },
  props: {
  },
  data() {
    return {
      menuOpened: false,
      menuItems: [
        {id: 'new-project', name: 'New project...', children: []},
        {id: 'image', name: 'Image', children: [
            {id: 'clear-current-layer', name: 'Clear current layer', children: []},
            {id: 'fill-white', name: 'Fill with white', children: []},
            {id: 'fill-black', name: 'Fill with black', children: []},
            {id: 'fill-with-current-fill-style', name: 'Fill with current fillStyle', children: []},
            {id: 'invert-colors', name: 'Invert colors', children: []},
            {id: 'invert-alpha', name: 'Invert alpha', children: []},
            {id: 'flip-horizontally', name: 'Flip horizontally', children: []},
            {id: 'flip-vertically', name: 'Flip vertically', children: []}
        ]},
        {id: 'layer', name: 'Layer', children: [
            {id: 'add-layer', name: 'Add layer', children: []},
            {id: 'duplicate-layer', name: 'Duplicate layer', children: []},
            {id: 'delete-layer', name: 'Delete layer', children: []}
        ]},
        {id: 'draw', name: 'Draw', children: [
          {id: 'text', name: 'Text', children: [
            {id: 'draw-text', name: 'Draw text...', children: []},
            {id: 'draw-stroke-text', name: 'Draw stroke text...', children: []},
            {id: 'draw-shadow-text', name: 'Draw shadow text...', children: []},
            {id: 'glow-text', name: 'Glow text...', children: []},
            {id: 'draw-3d-text', name: 'Draw 3D text...', children: []},
            {id: 'logo-01', name: 'Logo 01...', children: []},
            {id: 'logo-02', name: 'Logo 02...', children: []},
            {id: 'logo-03', name: 'Logo 03...', children: []},
            {id: 'logo-04', name: 'Logo 04...', children: []},
            {id: 'logo-05', name: 'Logo 05...', children: []},
            {id: 'logo-06', name: 'Logo 06...', children: []},
            {id: 'logo-07', name: 'Logo 07...', children: []},
            {id: 'logo-08', name: 'Logo 08...', children: []},
            {id: 'logo-09', name: 'Logo 09...', children: []}
          ]},
          {id: 'buttons', name: 'Buttons', children: [
            {id: 'round-button', name: 'Round button...', children: []},
            {id: 'round-button-2', name: 'Round button 2...', children: []},
            {id: 'gradient-round-button', name: 'Gradient round button...', children: []},
            {id: 'gradient-round-button-2', name: 'Gradient round button 2...', children: []},
            {id: '3d-gradient-round-button', name: '3D gradient round button...', children: []}
          ]},
          {id: 'draw-line', name: 'Draw line...', children: []},
          {id: 'draw-circle', name: 'Draw circle...', children: []},
          {id: 'fill-circle', name: 'Fill circle...', children: []},
          {id: 'draw-rect', name: 'Draw rect...', children: []},
          {id: 'fill-rect', name: 'Fill rect...', children: []}
        ]},
        {id: 'filters', name: 'Filters', children: [
            {id: 'color', name: 'Color', children: [
                {id: 'grayscale', name: 'Grayscale', children: []},
                {id: 'sepia', name: 'Sepia', children: []},
                {id: 'lighter', name: 'Lighter...', children: []},
                {id: 'darker', name: 'Darker...', children: []},
                {id: 'set-image-color', name: 'Set image color...', children: []},
                {id: 'modulate-image-color', name: 'Modulate image color...', children: []}
            ]},
            {id: 'border', name: 'Border', children: [
                {id: 'add-border', name: 'Add border...', children: []},
                {id: 'add-double-border', name: 'Add double border...', children: []},
                {id: 'add-color-border', name: 'Add color border...', children: []}
            ]},
            {id: 'blur', name: 'Blur...', children: []},
            {id: 'grow', name: 'Grow...', children: []},
            {id: 'noise', name: 'Noise...', children: []},
            {id: 'sunburst-effect', name: 'Sunburst effect...', children: []}
        ]},
        {id: 'scripts', name: 'Scripts', children: [
            {id: 'new-script', name: 'New script...', children: []}
        ]},
        {id: 'export', name: 'Export...', children: []},
      ],
      currentMenuItem: null
    }
  },
  mounted () {
    
    window.$('.dropdown-menu').click(function(e) {
      e.stopPropagation();
    });

    window.$('#navbarDropdownMenuLink').click(function(e) {
      e.stopPropagation();
    });

    let that = this;
    window.$(document).click(function(e) { // eslint-disable-line no-unused-vars
      that.resetMenu();
    });

  },
  methods: {
    onMainMenuClick() {
      if (this.menuOpened) {
        this.resetMenu();
      } else {
        this.openMainMenu();
      }
    },
    hideMenuItem(elementId) {
      let element = document.getElementById(elementId + "-children");
      if (element != null) {
        element.classList.remove("show");
      }
    },
    showMenuItem(elementId) {
      let element = document.getElementById(elementId + "-children");
      if (element != null) {
        element.classList.add("show");
      }
    },
    selectMenuItem(elementId) {
      let element = document.getElementById(elementId);
      if (element != null) {
        element.classList.add("dropdown-item-selected");
      }
    },
    unselectMenuItem(elementId) {
      let element = document.getElementById(elementId);
      if (element != null) {
        element.classList.remove("dropdown-item-selected");
      }
    },
    unselectMenuItemsLevel1() {
      for (let i=0; i<this.menuItems.length; i++) {
        let menuItem = this.menuItems[i];
        this.unselectMenuItem("submenu-" + menuItem.id);
      }
      this.unselectMenuItemsLevel2();
    },
    unselectMenuItemsLevel2() {
      for (let i=0; i<this.menuItems.length; i++) {
        let menuItem = this.menuItems[i];
        for (let j=0; j<menuItem.children.length; j++) {
          let child = menuItem.children[j];
          this.unselectMenuItem("subsubmenu-" + child.id);
        }
      }
    },
    selectMenuItemLevel1(elementId) {
      this.unselectMenuItemsLevel1();
      this.selectMenuItem(elementId);
    },
    selectMenuItemLevel2(elementId) {
      this.unselectMenuItemsLevel2();
      this.selectMenuItem(elementId);
    },
    openMainMenu() {
      this.showMenuItem("main-menu");
      this.menuOpened = true;
    },
    resetMenu() {
      this.hideMenuItem("main-menu");
      this.resetMenuLevel2();
      this.unselectMenuItemsLevel1();
      this.menuOpened = false;
    },
    resetMenuLevel2() {
      for (let i=0; i<this.menuItems.length; i++) {
        let menuItem = this.menuItems[i];
        this.hideMenuItem("submenu-" + menuItem.id);
      }
      this.resetMenuLevel3();
    },
    resetMenuLevel3() {
      for (let i=0; i<this.menuItems.length; i++) {
        let menuItem = this.menuItems[i];
        for (let j=0; j<menuItem.children.length; j++) {
          let child = menuItem.children[j];
          this.hideMenuItem("subsubmenu-" + child.id);
        }
      }
    },
    mouseOver(menuItem) {
      this.resetMenuLevel2();
      this.showMenuItem(menuItem);
      this.selectMenuItemLevel1(menuItem);
    },
    mouseOverLevel2(menuItem) {
      this.resetMenuLevel3();
      this.showMenuItem(menuItem);
      this.selectMenuItemLevel2(menuItem);
    },
    fillScreenWhite() {
      this.resetMenu();
      this.$parent.fillScreenWhite();
    },
    fillScreenBlack() {
      this.resetMenu();
      this.$parent.fillScreenBlack();
    },
    clearCurrentLayer() {
      this.resetMenu();
      this.$parent.clearCurrentLayer();
    },
    onMenuItemClick(menuItemId) {

      if (menuItemId == "new-project") {
        window.location.reload();
      } else if (menuItemId == "fill-white") {
        this.$parent.fillScreenWhite();
      } else if (menuItemId == "fill-black") {
        this.$parent.fillScreenBlack();
      } else if (menuItemId == "fill-with-current-fill-style") {
        this.$parent.executeSampleScript('Fill with current fillStyle',{});
      } else if (menuItemId == "clear-current-layer") {
        this.$parent.clearCurrentLayer();
      } else if (menuItemId == "export") {
        this.$parent.showExportModal();
      } else if (menuItemId == "add-layer") {
        this.$parent.$refs.layersPanel.showAddLayerForm();
      } else if (menuItemId == "duplicate-layer") {
        this.$parent.$refs.layersPanel.showDuplicateLayerForm();
      } else if (menuItemId == "delete-layer") {
        this.$parent.deleteCurrentLayer();
      } else if (menuItemId == "new-script") {
        this.$parent.createNewScript();
      } else if (menuItemId == "grayscale") {
        this.$parent.executeSampleScript('Grayscale layer',{});
      } else if (menuItemId == "sepia") {
        this.$parent.executeSampleScript('Repaint with filter', {filter: 'sepia(100%)'});
      } else if (menuItemId == "lighter") {
        this.$parent.selectSampleScriptWithParams('Lighter');
      } else if (menuItemId == "darker") {
        this.$parent.selectSampleScriptWithParams('Darker');
      } else if (menuItemId == "invert-colors") {
        this.$parent.executeSampleScript('Invert image colors', {});
      } else if (menuItemId == "invert-alpha") {
        this.$parent.executeSampleScript('Invert image alpha', {});
      } else if (menuItemId == "blur") {
        this.$parent.selectSampleScriptWithParams('Blur layer');
      } else if (menuItemId == "grow") {
        this.$parent.selectSampleScriptWithParams('Grow');
      } else if (menuItemId == "set-image-color") {
        this.$parent.selectSampleScriptWithParams('Set image color');
      } else if (menuItemId == "modulate-image-color") {
        this.$parent.selectSampleScriptWithParams('Modulate image color');
      } else if (menuItemId == "add-border") {
        this.$parent.selectSampleScriptWithParams('Add border');
      } else if (menuItemId == "add-double-border") {
        this.$parent.selectSampleScriptWithParams('Add double border');
      } else if (menuItemId == "add-color-border") {
        this.$parent.selectSampleScriptWithParams('Add color border');
      } else if (menuItemId == "noise") {
        this.$parent.selectSampleScriptWithParams('Noise');
      } else if (menuItemId == "sunburst-effect") {
        this.$parent.selectSampleScriptWithParams('Sunburst effect');
      } else if (menuItemId == "draw-line") {
        this.$parent.selectSampleScriptWithParams('Draw line');
      } else if (menuItemId == "draw-circle") {
        this.$parent.selectSampleScriptWithParams('Draw circle');
      } else if (menuItemId == "fill-circle") {
        this.$parent.selectSampleScriptWithParams('Fill circle');
      } else if (menuItemId == "draw-rect") {
        this.$parent.selectSampleScriptWithParams('Draw rect');
      } else if (menuItemId == "fill-rect") {
        this.$parent.selectSampleScriptWithParams('Fill rect');
      } else if (menuItemId == "draw-text") {
        this.$parent.selectSampleScriptWithParams('Draw text');
      } else if (menuItemId == "draw-stroke-text") {
        this.$parent.selectSampleScriptWithParams('Draw stroke text');
      } else if (menuItemId == "glow-text") {
        this.$parent.selectSampleScriptWithParams('Glow text');
      } else if (menuItemId == "draw-3d-text") {
        this.$parent.selectSampleScriptWithParams('Draw 3D text');
      } else if (menuItemId == "draw-shadow-text") {
        this.$parent.selectSampleScriptWithParams('Draw shadow text');
      } else if (menuItemId == "logo-01") {
        this.$parent.selectSampleScriptWithParams('Logo 01');
      } else if (menuItemId == "logo-02") {
        this.$parent.selectSampleScriptWithParams('Logo 02');
      } else if (menuItemId == "logo-03") {
        this.$parent.selectSampleScriptWithParams('Logo 03');
      } else if (menuItemId == "logo-04") {
        this.$parent.selectSampleScriptWithParams('Logo 04');
      } else if (menuItemId == "logo-05") {
        this.$parent.selectSampleScriptWithParams('Logo 05');
      } else if (menuItemId == "logo-06") {
        this.$parent.selectSampleScriptWithParams('Logo 06');
      } else if (menuItemId == "logo-07") {
        this.$parent.selectSampleScriptWithParams('Logo 07');
      } else if (menuItemId == "logo-08") {
        this.$parent.selectSampleScriptWithParams('Logo 08');
      } else if (menuItemId == "logo-09") {
        this.$parent.selectSampleScriptWithParams('Logo 09');
      } else if (menuItemId == "round-button") {
        this.$parent.selectSampleScriptWithParams('Round button');
      } else if (menuItemId == "round-button-2") {
        this.$parent.selectSampleScriptWithParams('Round button 2');
      } else if (menuItemId == "gradient-round-button") {
        this.$parent.selectSampleScriptWithParams('Gradient round button');
      } else if (menuItemId == "gradient-round-button-2") {
        this.$parent.selectSampleScriptWithParams('Gradient round button 2');
      } else if (menuItemId == "3d-gradient-round-button") {
        this.$parent.selectSampleScriptWithParams('3D gradient round button');
      } else if (menuItemId == "flip-horizontally") {
        this.$parent.executeSampleScript('Flip horizontally',{});
      } else if (menuItemId == "flip-vertically") {
        this.$parent.executeSampleScript('Flip vertically',{});
      } else { // Clicked on an intermediate submenu
        return;
      }

      this.resetMenu();

    }
  }
}
</script>

