<template>
  <div>
    <div>
        <div class="panelHeader" v-on:click="$parent.togglePanelVisibility('events')">
            <span class="panelTitle">Events</span>
        </div>
        <div v-show="$parent.visiblePanels.events" class="panelBody">
            <table class="smallTable">
                <thead>
                    <tr>
                        <th>enabled</th>
                        <th>name</th>
                        <th>type</th>
                        <th>execute</th>
                        <th>edit</th>
                        <th>delete</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="customEvent in $parent.customEvents" v-bind:key="customEvent.name">
                        <td>
                            <div class="custom-control custom-switch" style="text-align: center;">
                                <input type="checkbox" v-model="customEvent.enabled" class="custom-control-input" :id="'_custom_event_enabled_' + customEvent.name">
                                <label class="custom-control-label" :for="'_custom_event_enabled_' + customEvent.name"></label>
                            </div>
                        </td>
                        <td>{{ customEvent.name }}</td>
                        <td>{{ customEvent.type }}</td>
                        <td><button v-on:click="executeCustomEvent(customEvent)" class="btn btn-primary btn-sm">execute</button></td>
                        <td><button v-on:click="showEditCustomEventForm(customEvent)" data-toggle="modal" data-target="#modalEditCustomEvent" class="btn btn-primary btn-sm">edit</button></td>
                        <td><button v-on:click="deleteCustomEvent(customEvent)" class="btn btn-danger btn-sm">delete</button></td>
                    </tr>
                </tbody>
            </table>
            <button v-on:click="showAddCustomEventForm()" data-toggle="modal" data-target="#modalAddCustomEvent" class="btn btn-primary btn-sm">Add event</button>
        </div>
    </div>


    <!-- Modal AddCustomEvent -->
    <div class="modal fade" id="modalAddCustomEvent" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="modalLabel">Add event</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>Name: <input v-model="newEventForm.name"></div>
            <div>
              Type:
              <select v-model="newEventForm.type">
                  <option v-for="type in customEventTypes" v-bind:value="type" v-bind:key="type">
                      {{ type }}
                  </option>
              </select>
            </div>
            <div>Script:</div>
            <div class="divSourceCodeWithHelp">
              <textarea v-model="newEventForm.sourceCode" id="sourceCode" style="width: 100%; height: 240px;"></textarea>
              <div>
                <div><b>To get current mouse position:</b></div>
                <div>app.mousePosition.x</div>
                <div>app.mousePosition.y</div>
                <div><b>To get previous mouse position:</b></div>
                <div>app.previousMousePosition.x</div>
                <div>app.previousMousePosition.y</div>
                <div><b>To get last mouse down position:</b></div>
                <div>app.mouseDownPosition.x</div>
                <div>app.mouseDownPosition.y</div>
                <div><b>To detect if mouse button is down:</b></div>
                <div>app.isMouseDown</div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="addCustomEvent()">Save</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
          </div>
        </div>
      </div>
    </div>


    <!-- Modal EditCustomEvent -->
    <div class="modal fade" id="modalEditCustomEvent" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="modalLabel">Edit event</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>Name: <input v-model="editEventForm.name"></div>
            <div>
              Type:
              <select v-model="editEventForm.type">
                  <option v-for="type in customEventTypes" v-bind:value="type" v-bind:key="type">
                      {{ type }}
                  </option>
              </select>
            </div>
            <div>Script:</div>
            <div class="divSourceCodeWithHelp">
              <textarea v-model="editEventForm.sourceCode" id="sourceCode" style="width: 100%; height: 240px;"></textarea>
              <div>
                <div><b>To get current mouse position:</b></div>
                <div>app.mousePosition.x</div>
                <div>app.mousePosition.y</div>
                <div><b>To get previous mouse position:</b></div>
                <div>app.previousMousePosition.x</div>
                <div>app.previousMousePosition.y</div>
                <div><b>To get last mouse down position:</b></div>
                <div>app.mouseDownPosition.x</div>
                <div>app.mouseDownPosition.y</div>
                <div><b>To detect if mouse button is down:</b></div>
                <div>app.isMouseDown</div>
              </div>
            </div>
            <div class="custom-control custom-switch" style="margin-top: 10px;">
                <input type="checkbox" v-model="editEventForm.enabled" class="custom-control-input" :id="'_edit_custom_event_enabled_' + editEventForm.name">
                <label class="custom-control-label" :for="'_edit_custom_event_enabled_' + editEventForm.name">Enabled</label>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updateCustomEvent()">Save</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
//import IconUp from '@/components/icons/IconUp.vue'
//import IconDown from '@/components/icons/IconDown.vue'

export default {
  name: 'EventsPanel',
  components: {
    //IconUp, IconDown
  },
  props: {
  },
  data() {
    return {
      customEventTypes: ['mouseDown','mouseUp','mouseMove','animationFrame'],
      originalEvent: null,
      editEventForm: {
        name: null,
        type: null,
        enabled: null,
        sourceCode: null
      },
      newEventForm: {
        name: null,
        type: null,
        enabled: null,
        sourceCode: null
      }
    }
  },
  methods: {
    showAddCustomEventForm() {
      this.newEventForm.name = "Event 01";
      this.newEventForm.type = this.customEventTypes[0];
      this.newEventForm.sourceCode = "";
      this.newEventForm.enabled = true;
    },
    addCustomEvent() {
      let customEvent = {
        name: this.newEventForm.name,
        type: this.newEventForm.type,
        enabled: this.newEventForm.enabled,
        func: new Function("params", this.newEventForm.sourceCode),
        source: this.newEventForm.sourceCode
      };
      this.$parent.customEvents.push(customEvent);
    },
    showEditCustomEventForm(customEvent) {
      this.originalEvent = customEvent;

      this.editEventForm.name = customEvent.name;
      this.editEventForm.type = customEvent.type;
      this.editEventForm.enabled = customEvent.enabled;
      this.editEventForm.sourceCode = customEvent.source;
    },
    updateCustomEvent() {
      this.originalEvent.name = this.editEventForm.name;
      this.originalEvent.type = this.editEventForm.type;
      this.originalEvent.source = this.editEventForm.sourceCode;
      this.originalEvent.enabled = this.editEventForm.enabled;
      this.originalEvent.func = new Function("params", this.editEventForm.sourceCode);
    },
    executeCustomEvent(customEvent) {
      customEvent.func();
      this.$parent.updateCanvasAllLayers();
      this.$parent.updateCurrentLayerCanvas();
    },
    deleteCustomEvent(customEvent) {
      this.$parent.deleteCustomEvent(customEvent);
    }
  },
}
</script>

